import {
  Routes,
  Route,
  useNavigationType,
  useLocation,
} from "react-router-dom";
import Phone from "./pages/Phone";
import MiniPhone from "./pages/MiniPhone";
import Tablet from "./pages/Tablet";
import Laptop from "./pages/Laptop";
import Desktop from "./pages/Desktop";
import Divframer72rtr7 from "./pages/Divframer72rtr7";
import PxDesktop from "./pages/PxDesktop";
import { useEffect } from "react";

function App() {
  const action = useNavigationType();
  const location = useLocation();
  const pathname = location.pathname;

  useEffect(() => {
    if (action !== "POP") {
      window.scrollTo(0, 0);
    }
  }, [action, pathname]);

  useEffect(() => {
    let title = "";
    let metaDescription = "";

    switch (pathname) {
      case "/":
        title = "";
        metaDescription = "";
        break;
      case "/miniphone":
        title = "";
        metaDescription = "";
        break;
      case "/tablet":
        title = "";
        metaDescription = "";
        break;
      case "/laptop":
        title = "";
        metaDescription = "";
        break;
      case "/desktop":
        title = "";
        metaDescription = "";
        break;
      case "/divframer72rtr7":
        title = "";
        metaDescription = "";
        break;
      case "/1440px-desktop":
        title = "";
        metaDescription = "";
        break;
    }

    if (title) {
      document.title = title;
    }

    if (metaDescription) {
      const metaDescriptionTag: HTMLMetaElement | null = document.querySelector(
        'head > meta[name="description"]'
      );
      if (metaDescriptionTag) {
        metaDescriptionTag.content = metaDescription;
      }
    }
  }, [pathname]);

  return (
    <Routes>
      <Route path="/" element={<Phone />} />
      <Route path="/miniphone" element={<MiniPhone />} />
      <Route path="/tablet" element={<Tablet />} />
      <Route path="/laptop" element={<Laptop />} />
      <Route path="/desktop" element={<Desktop />} />
      <Route path="/divframer72rtr7" element={<Divframer72rtr7 />} />
      <Route path="/1440px-desktop" element={<PxDesktop />} />
    </Routes>
  );
}
export default App;
